#page-content .center {
    width: 750px !important; }

.list {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    border: 1px solid #EEE;
    border-radius: 4px;
    -moz-border-radius: 4px;
    -webkit-border-radius: 4px; }

.list a {
    display: flex;
    flex: 1 0 35%;
    align-items: center;
    text-decoration: none;
    border: 1px solid transparent;
    border-radius: 4px;
    -moz-border-radius: 4px;
    -webkit-border-radius: 4px; }

.list a:empty {
    height: 0;
    width: 0; }

.list a div {
    flex-basis: 33%; }

.list a div img {
    float: left;
    margin: 7px; }

.list a span {
    padding-left: 20px; }

.list a:hover {
    background-color: #F8F8FB; }

@media screen and (max-width: 600px) {
    .list a {
        flex: 1 0 100%; } }
